.main__banner {
  display: flex;
  flex-direction: column;
  width: 100%;
  overflow: hidden;

  // outline: 1px solid orangered;
  // outline-offset: -1px;
}

.banner__container {
  position: relative;
  display: flex;
  flex-direction: column;
  align-items: center;
  width: 88%;
  margin: 0 auto;
  background-image: url("../img/banner-mobile.png");

  @supports (background-image: url("../img/banner-mobile.webp")) {
    background-image: url("../img/banner-mobile.webp");
  }

  background-repeat: no-repeat;
  background-size: 90% auto;
  background-position: center 140px;

  @media (min-resolution: 2dppx) {
    background-image: url("../img/banner-mobile@2x.png");

    @supports (background-image: url("../img/banner-mobile@2x.webp")) {
      background-image: url("../img/banner-mobile@2x.webp");
    }
  }

  @media (min-width: @modile_xl_width) {
    background-position: center 160px;
  }

  @media (min-width: @tablet_width) {
    align-items: flex-start;
    background-size: 380px auto;
    background-position: 100% 50px;
    background-image: url("../img/banner-tablet.png");

    @supports (background-image: url("../img/banner-tablet.webp")) {
      background-image: url("../img/banner-tablet.webp");
    }

    @media (min-resolution: 2dppx) {
      background-image: url("../img/banner-tablet@2x.png");

      @supports (background-image: url("../img/banner-tablet@2x.webp")) {
        background-image: url("../img/banner-tablet@2x.webp");
      }
    }
  }

  @media (min-width: @laptop_width) {
    width: calc(~'30% + 624px');
    background-size: 450px auto;
    background-position: 100% 90px;
    background-image: url("../img/banner-desktop.png");

    @supports (background-image: url("../img/banner-desktop.webp")) {
      background-image: url("../img/banner-desktop.webp");
    }

    @media (min-resolution: 2dppx) {
      background-image: url("../img/banner-desktop@2x.png");

      @supports (background-image: url("../img/banner-desktop@2x.webp")) {
        background-image: url("../img/banner-desktop@2x.webp");
      }
    }
  }

  @media (min-width: @desktop_width) {
    width: calc(~'35% + 763px');
    background-size: 520px auto;
    background-position: 100% 90px;
  }
}

.banner__intro {
  display: none;

  @media (min-width: @laptop_width) {
    display: block;
    width: max-content;
    background-color: @special_pink;
    padding: .2em 1em;
    border-radius: 16px;

    .font-medium;
    font-size: 1em;
    line-height: 1.4em;
    color: @basic_gray;
    margin: 110px 0 0 0;
    text-align: start;
  }

  @media (min-width: @desktop_width) {
    border-radius: 20.5px;

    font-size: 1.2em;
    line-height: 1.5em;
    color: @basic_gray;
    margin: 90px 0 0 0;
  }
}

.banner__heading {
  width: 278px;
  .font-bold;
  font-size: 1.15em;
  line-height: 1.4em;
  color: @basic_blue;
  margin: 30px 0 0 0;
  text-align: center;
  z-index: 2;

  @media (min-width: @modile_xl_width) {
    width: 350px;
    font-size: 1.5em;
    margin: 35px 0 0 0;
  }

  @media (min-width: @tablet_width) {
    width: 380px;
    font-size: 1.55em;
    line-height: 1.5em;
    margin: 50px 0 0 0;
    text-align: start;
  }

  @media (min-width: @laptop_width) {
    width: 440px;
    font-size: 2em;
    line-height: 1.55em;
    margin: 30px 0 0 0;
  }

  @media (min-width: @desktop_width) {
    width: 560px;
    font-size: 2.5em;
    line-height: 1.55em;
    margin: 40px 0 0 0;
  }
}

.banner__text {
  width: 278px;
  margin: calc(~'85vw + 50px') 0 0 0;

  .font-normal;
  font-size: .91em;
  line-height: 1.4em;
  color: @basic_gray;
  text-align: center;

  @media (min-width: @modile_xl_width) {
    width: 340px;
    font-size: 1.1em;
    margin: calc(~'85vw + 70px') 0 0 0;
  }

  @media (min-width: @tablet_width) {
    width: 290px;
    margin-top: 30px;
    font-size: 1.2em;
    line-height: 1.5em;
    text-align: start;
    z-index: 2;
  }

  @media (min-width: @laptop_width) {
    width: 370px;
    margin-top: 60px;
  }

  @media (min-width: @desktop_width) {
    width: 450px;
    margin-top: 100px;
    font-size: 1.4em;
  }
}

.banner__sign {
  position: relative;
  display: flex;
  flex-direction: row;
  width: 100%;
  height: 60px;
  margin: 20px 0 30px 0;
  background-color: @basic_pink;
  border-radius: 30px;
  text-decoration: none;
  z-index: 2;
  overflow: hidden;
  transition: box-shadow 0.3s, transform 0.3s;

  @media (min-width: @modile_xl_width) {
    height: 70px;
    margin: 30px 0 40px 0;
    border-radius: 35px;
  }

  @media (min-width: @tablet_width) {
    width: 45%;
    margin: 64px 0 40px 0;
  }

  @media (min-width: @laptop_width) {
    width: 360px;
    margin: 30px 0 90px 0;
  }

  @media (min-width: @desktop_width) {
    width: 400px;
    margin: 40px 0 90px 0;
  }
}

.banner__sign:hover {
  transform: translateY(-4px);
  box-shadow: 0 4px 12px @translucent_black, 0 16px 20px @translucent_black;
}

.banner__sign:active,
.banner__sign:focus {
  transform: translateY(0);
  box-shadow: none;
}

.banner__sign:before {
  content: "";
  position: absolute;
  top: 0;
  left: -100%;
  width: 100%;
  height: 100%;
  background: linear-gradient(-45deg, transparent, @special_pink, transparent);
  transition: left 0.7s;
}

.banner__sign:hover:before,
.banner__sign:active:before,
.banner__sign:focus:before {
  left: 100%;
}

.sign__text {
  width: calc(~'90% - 60px');
  background-color: @basic_pink;
  border-radius: 30px;
  padding: 0 0 0 10%;
  margin: 0;

  .font-medium;
  font-size: 1.1em;
  line-height: 56px;
  color: @basic_white;
  text-align: center;

  @media (min-width: @modile_xl_width) {
    font-size: 1.3em;
    line-height: 66px;
  }

  @media (min-width: @tablet_width) {
    font-size: 1.35em;
    padding: 0 0 0 8%;
  }

  @media (min-width: @desktop_width) {
    font-size: 1.45em;
  }
}

.banner__sign:hover .sign__text {
  color: @basic_blue;
}

.banner__sign:active .sign__text,
.banner__sign:focus .sign__text {
  color: @basic_white;
}

.sign__icon {
  width: 56px;
  height: 56px;
  border-radius: 50%;
  border: 2px solid @basic_pink;
  background-color: @basic_white;

  @media (min-width: @modile_xl_width) {
    width: 64px;
    height: 64px;
    border: 3px solid @basic_pink;
  }
}

.sign__icon-arrow {
  position: relative;
  display: block;
  width: 40%;
  height: 40%;
  border-bottom: 4px solid @basic_blue;
  border-right: 4px solid @basic_blue;
  transform: rotate(45deg);
  top: 30%;
  left: 29%;
  animation: pulse1 2s infinite;
}

.sign__icon-arrow::after {
  position: absolute;
  content: "";
  width: 4px;
  height: 160%;
  background-color: @basic_blue;
  transform: rotate(-45deg);
  top: -30%;
  left: 40%;
  animation: pulse2 2s infinite;
}

.banner__sign:hover .sign__icon-arrow,
.banner__sign:hover .sign__icon-arrow::after {
  animation: none;
}

@keyframes pulse1 {
  0% {
    transform: rotate(45deg) scale(1);
  }

  50% {
    transform: rotate(45deg) scale(1.2);
  }

  100% {
    transform: rotate(45deg) scale(.9);
  }
}

@keyframes pulse2 {
  0% {
    transform: rotate(-45deg) scale(1);
  }

  50% {
    transform: rotate(-45deg) scaleX(1.2);
  }

  100% {
    transform: rotate(-45deg) scale(.9);
  }
}

.banner__canvas {
  position: absolute;
  top: calc(~'9% + 95px');
  left: calc(~'50% - 10px');

  @media (min-width: @tablet_width) {
    top: 64px;
    left: calc(~'100% - 196px');
  }

  @media (min-width: @laptop_width) {
    top: 105px;
    left: calc(~'100% - 216px');
  }

  @media (min-width: @desktop_width) {
    top: 107px;
    left: calc(~'100% - 250px');
  }
}

.canvas-wrap {
  position: relative;
  padding: 10px;
}

#canvas {
  position: absolute;
  top: 0;
  right: 0;
  bottom: 0;
  left: 0;
}

.plane {
  width: 38.44vw;
  height: 12.84vw;

  @media (min-width: @tablet_width) {
    width: 177.67px;
    height: 59.37px;
  }

  @media (min-width: @desktop_width) {
    width: 205.01px;
    height: 68.51px;
  }
}

.canvas__photo {
  display: none;
}

.banner__cloud-1 {
  position: absolute;
  left: 10%;
  bottom: 30%;
  height: 7vw;
  animation: cloud1 24s infinite linear;

  @media (min-width: @tablet_width) {
    bottom: 30px;
    height: 30px;
    animation: cloud1 30s infinite linear;
  }

  @media (min-width: @laptop_width) {
    bottom: 150px;
    height: 33px;
    animation: cloud1 38s infinite linear;
  }

  @media (min-width: @desktop_width) {
    bottom: 250px;
    height: 56px;
    animation: cloud1 46s infinite linear;
  }
}

.banner__cloud-2 {
  position: absolute;
  left: -100vw;
  bottom: 37%;
  height: 8vw;
  animation: cloud2 24s infinite linear;

  @media (min-width: @tablet_width) {
    bottom: 150px;
    height: 50px;
    animation: cloud2 30s infinite linear;
  }

  @media (min-width: @laptop_width) {
    bottom: 260px;
    height: 55px;
    animation: cloud2 38s infinite linear;
  }

  @media (min-width: @desktop_width) {
    bottom: 230px;
    height: 50px;
    animation: cloud2 46s infinite linear;
    animation-delay: -7s;
  }
}

.banner__cloud-3 {
  display: none;

  @media (min-width: @tablet_width) {
    display: block;
    position: absolute;
    left: -100vw;
    bottom: 100px;
    height: 40px;
    animation: cloud2 30s infinite linear;
    animation-delay: 9s;
  }

  @media (min-width: @laptop_width) {
    bottom: 200px;
    height: 45px;
    animation: cloud2 38s infinite linear;
    animation-delay: 12s;
  }

  @media (min-width: @desktop_width) {
    bottom: 370px;
    width: 100%;
    height: 45px;
    animation: cloud2 46s infinite linear;
    animation-delay: 1s;
  }
}

.banner__cloud-4 {
  display: none;

  @media (min-width: @desktop_width) {
    display: block;
    position: absolute;
    left: -100vw;
    bottom: 275px;
    width: 100%;
    height: 100px;
    animation: cloud2 46s infinite linear;
    animation-delay: 9s;
  }
}

@keyframes cloud1 {
  0% {
    opacity: 1;
  }

  46% {
    transform: translateX(100vw);
    opacity: 1;
  }

  48% {
    transform: translateX(100vw);
    opacity: 0;
  }

  49% {
    transform: translateX(-100vw);
    opacity: 0;
  }

  54% {
    transform: translateX(-100vw);
    opacity: 1;
  }

  100% {
    opacity: 1;
  }
}

@keyframes cloud2 {
  0% {
    opacity: 1;
  }

  92% {
    transform: translateX(200vw);
    opacity: 1;
  }

  93% {
    transform: translateX(200vw);
    opacity: 0;
  }

  94% {
    transform: translateX(-100vw);
    opacity: 0;
  }

  95% {
    transform: translateX(-100vw);
    opacity: 1;
  }

  100% {
    opacity: 1;
  }
}
